export const required = (value) => (value ? undefined : " ");

export const minLength = (min) => (value) =>
  value && value.length < min ? `  ${min} ` : undefined;

export const maxLength = (max) => (value) =>
  value && value.length > max ? ` ${max} ` : undefined;

export const combinedValidators = (...validators) => (value) =>
  validators.reduce((err, val) => err || val(value), undefined);
